import React, { Component } from 'react';
import './index.css';
import ImageColumn from '../ImageColumn';

// Елементи з БД і їх обчислення
import _ from 'underscore';

class Images extends Component {  
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {  dataUrl: [], show: false };
  };
  
  componentDidMount() {  
    const chunk = (arr) => {        
        
      var newArr = [[],[],[],[]];
      var i = 0;
      
      arr.forEach(x => {
        newArr[i].push(x);
        i++;
        if( i === 4 ) i = 0;    
      });
      
      return newArr;
    };

    if( this.props.urls.length > 0 ) {      
      let arrShow = chunk( this.props.urls );
      this.setState({ dataUrl: arrShow, show: true });
    };
  };

  render() {       
    const { dataUrl } = this.state;    
    return (
      <div>        
          { 
            this.state.show 
            ?            
            <div className="row">
              <ImageColumn dataurl={ dataUrl[0] } />
              <ImageColumn dataurl={ dataUrl[1] } />
              <ImageColumn dataurl={ dataUrl[2] } />
              <ImageColumn dataurl={ dataUrl[3] } />         
            </div>            
            :<div>LOADING...</div>
          }
      </div>
    );
  };
};
  
export default Images;


